import React , { Component } from 'react'
import Guitar from '../img/guitar.png'
import Bakery from '../img/bakery.png'
import Motor from '../img/momotors.png'
import Intra from '../img/intra.png'
import Services from '../img/services.png'
import Access from '../img/access.png'
import Market from '../img/market.png'
import Airshoes from '../img/airshoes.png'
import Aljon from '../img/aljon.png'
import PetShop from '../img/pet.png'
import Versett from '../img/versett.png'
import Tilt from 'react-tilt'


   
     

class Works extends Component{
    
    intra(){
        window.open('https://christianjerr.github.io/intra' , '_blank')
    }
    
    pet(){
        window.open('https://christianjerr.github.io/petclinic' , '_blank')
    }
    
    services(){
         window.open('https://jptorrecarion.github.io/mysite/ ' , '_blank')
    }
    
    access(){
         window.open('https://christianjerr.github.io/accessofficial ' , '_blank')
    }
    
    market(){
         window.open('https://christianjerr.github.io/market' , '_blank')
    }
    
 
        
    guitar(){
        window.open('https://christianjerr.github.io/guitar' , '_blank')
    }
    
    bakery(){
         window.open('https://christianjerr.github.io/tengsbakery' , '_blank')
    }
    
    momotors(){
         window.open('https://christianjerr.github.io/momotors' , '_blank')
    }
    
    aljon(){
         window.open('https://christianjerr.github.io/photography' , '_blank')
    }
    
   airshoes(){
         window.open('https://christianjerr.github.io/airshoes' , '_blank')
    }
    
    versett(){
         window.open('https://christianjerr.github.io/versett' , '_blank')
    }
    

    
    
    
    render(){
     
        
        return(
            
            <div className = ' top works text-light' id='works'>
           
            <div className='container'>
                <div className = 'row'>
                    <div className = 'col-md-4'>
            <h1 className = 'font-weight-bold'>Recent Work</h1>
                    </div>
                    <div className = 'col-md-8'>
            <p>Here are some works that i had accomplished in the past few days for my clients. <small className = 'text-success'>* some of my other works are not included in this section</small></p>
                    </div>
                </div>
            
            
            
                <div className='row top text-center' >
                   
                    
                    <Tilt className="Tilt col-md-3" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                   <div className=' Tilt-inner'><img alt='img' src = {Intra} className='brand' onClick = { this.intra }/></div>
                    </Tilt>
                    
                      <Tilt className="Tilt" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                       <div className='col-md-3 Tilt-inner'><img alt='img' src = {Guitar} className='brand' onClick = { this.guitar }/></div>
                    
                    </Tilt>
            
                     <Tilt className="Tilt" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                       <div className='col-md-3 Tilt-inner'><img alt='img' src = {Bakery} className='brand' onClick = {this.bakery}/></div>
                    </Tilt>
            
            
                    <Tilt className="Tilt" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                        <div className='col-md-3 Tilt-inner'><img alt='img' src = {Motor} className='brand' onClick = { this.momotors } /></div>
                    </Tilt> 
                </div> 
             <div className='row '>
                    
                    <Tilt className="Tilt" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                        <div className='col-md-3 Tilt-inner'><img alt='img' src = {Versett} className='brand' onClick = { this.versett } /></div>
                    </Tilt>
                   
                    <Tilt className="Tilt" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                        <div className='col-md-3 Tilt-inner'><img alt='img' src = {Aljon} className='brand' onClick = { this.aljon } /></div>
                    </Tilt>
            
                    <Tilt className="Tilt" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                        <div className='col-md-3 Tilt-inner'><img alt='img' src = {PetShop} className='brand' onClick = { this.pet } /></div>
                    </Tilt>
                    
                    <Tilt className="Tilt" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                        <div className='col-md-3 Tilt-inner'><img alt='img' src = {Airshoes} className='brand' onClick = { this.airshoes } /></div>
                    </Tilt> 
                    <Tilt className="Tilt col-md-3" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                   <div className=' Tilt-inner'><img alt='img' src = {Market} className='brand' onClick = { this.market }/></div>
                    </Tilt>
            
                    <Tilt className="Tilt col-md-3" options={{ max : 50 }} style={{ height: 250, width: 250 }} >
                   <div className=' Tilt-inner'><img alt='img' src = {Services} className='brand' onClick = { this.services }/></div>
                    </Tilt>
            
                    <Tilt className="Tilt col-md-3" options={{ max : 50 }} style={{ height: 100, width: 250 , background : 'white' }} >
                   <div className=' Tilt-inner'><img alt='img' src = {Access} className='brand' onClick = { this.access }/></div>
                    </Tilt>
                    
                   
                    
            
            
                </div>
            
            
            </div>
            
            </div>
            
            
        )
    }
}
export default Works