import React , { Component } from 'react' 
import AnchorLink from 'react-anchor-link-smooth-scroll'
 


class Navbar extends Component{
    constructor(){
        super()
        this.state = {
            name : 'Navigation bar'
        }
    }
    
    render(){
        return(
            <div >
                <nav className="navbar fixed-top navbar-expand-lg navbar-dark">
                  <a className="navbar-brand" href="#home"><small><b><span className = 'primary'>CHRISTIAN JERR</span> BUARAO</b></small><br/></a>
                  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                  </button>

                  <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                      <li className="nav-item">
                        <AnchorLink className="nav-link" href="#home" offset='500'>HOME</AnchorLink>
                      </li>
                      <li className="nav-item">
                          
                    <AnchorLink className="nav-link" href="#about">ABOUT</AnchorLink>
                        
                      </li>
                      <li className="nav-item">
                        <AnchorLink className="nav-link" href="#contact" offset='60'>CONTACT</AnchorLink>
                      </li>
            
                        <li className="nav-item">
                        <AnchorLink className="nav-link" href="#works" offset='60'>WORKS</AnchorLink>
                      </li>
                    </ul>
                    
                  </div>
                </nav>
        
                
            </div>
           
        )
    }
}

export default Navbar
