import React , { Component } from 'react' 
import TextLoop from "react-text-loop";
import {AnimatedOnScroll} from "react-animated-css-onscroll";

class Navbar extends Component{
    constructor(){
        super()
        this.state = {
            name : 'Navigation bar'
        }
    }
    
    render(){
        
        
    let margin = {
        paddingTop : '300px' ,
        height : '100vh' ,
    }
    
  
   
        
        return(
            <div style = { margin} id='home' className  ='container'>
            
            <div  className='text-left text-light app_home'>
            
            <AnimatedOnScroll animationIn="zoomIn" animationOut="fadeOut">
    <div>
                <h4> Welcome, I am Christian Jerr <TextLoop>
                    <span className = 'primary'>i am a I.T. Fresh graduate,</span>
                    <span className = 'primary'>a Web Designer ,</span>
                    <span className = 'primary'>an Aspring Full Stack Developer ,</span>
                </TextLoop>{" "}


           </h4>
            
            <h4>I am  <span className='primary'>passionate</span> about front end development ,Let's turn your design into websites.
           </h4>
    </div>
</AnimatedOnScroll>
            
            
            </div>
            </div>
           
        )
    }
}

export default Navbar
