import React, { Component } from 'react';
import Particles from 'react-particles-js';
import Navbar from './components/app_navbar'
import Showcase from './components/app_showcase'
import About from './components/app_about'
import Works from './components/app_works'
import Footer from './components/app_footer'



class App extends Component {
  render() {
      let particlesOpt = {
          particles : {
              number :{
                  value : 35 ,
                   density :{
                       enable : true,
                       value_area : 800
                   }
              }
          }
      }
      
      let particlesStyle ={
          position : 'absolute' ,
          zIndex : -1
      }
      
    return (
      <div>
        
        <Particles params = {particlesOpt} style = {particlesStyle}/>
        <Navbar/>
        <Showcase />
        <About />
        <Works />
        <Footer />
      </div>
    );
  }
}

export default App;
